//import classes from './arrangeCal.module.css'
import { useDispatch, useSelector } from "react-redux";

import Alert from "react-bootstrap/Alert";
import Dropdown from "react-bootstrap/Dropdown";
import { stageActions, calenderActions } from "../../store/store";
import classes from "./initinalDataInput.module.css";

import { useState } from "react";

import Buttons from "../buttons/buttons";

const InitialDataInput = () => {
  const dispatch = useDispatch();

  const [dataState, setDataState] = useState({
    nameCal: "Work",
    mounth: "Choose a month",
    disable: {
      monthSelected: false,
      nameSelcted: true,
    },
  });

  const chooseMouth = (id, month) => {
    const oldState = { ...dataState };
    setDataState({
      mounth: id,
      nameCal: dataState.nameCal,
      disable: {
        ...dataState.disable,
        monthSelected: true,
      },
    });
  };

  const onChangeHandler = (event) => {
    const disableCount = event.target.value.length < 3 ? false : true;
    setDataState({
      mounth: dataState.mounth,
      nameCal: event.target.value,
      disable: {
        ...dataState.disable,
        nameSelcted: disableCount,
      },
    });
  };

  const onClearHandler = () => {
    setDataState({
      nameCal: "Work",
      mounth: "Choose a month",
      disable: {
        monthSelected: false,
        nameSelcted: false,
      },
    });
  };

  const nextStepHandler = () => {
    dispatch(calenderActions.addDateAndName(dataState))
    dispatch(stageActions.nextStage());
  };

  const log = () => {
    console.log(dataState, disable);
  };

  const disable =
    dataState.disable.nameSelcted & (dataState.disable.monthSelected === true)
      ? false
      : true;

  const monthsNames = [
    "January",
    "February",
    "March",
    "April",
    "May",
    "June",
    "July",
    "August",
    "September",
    "October",
    "November",
    "December",
  ];
  const monthsNumbers = [
    "01",
    "02",
    "03",
    "04",
    "05",
    "06",
    "07",
    "08",
    "09",
    "10",
    "11",
    "12",
  ];

  return (
    <div className={classes.initData}>
      <Alert variant="primary">
        <p>Please choose :</p>
      </Alert>
      <div>
        <Dropdown>
          <Dropdown.Toggle variant="success" id="dropdown-basic">
            {dataState.mounth}
          </Dropdown.Toggle>
          <Dropdown.Menu>
            {monthsNames.map((el, ind) => {
              return (
                <Dropdown.Item
                  key={"Key" + el + monthsNumbers[ind]}
                  onClick={() => chooseMouth(monthsNumbers[ind], el)}>
                  {el} 2023
                </Dropdown.Item>
              );
            })}
          </Dropdown.Menu>
        </Dropdown>
      </div>

      <input
        value={dataState.nameCal}
        onChange={(val) => onChangeHandler(val)}
        type="text"
        id="name"
        name="name"
        requiredminlength="4"
        size="15"></input>
      <Buttons dis={disable} dis2={true} new={onClearHandler} next={nextStepHandler} />
    </div>
  );
};

export default InitialDataInput;
