import { stageActions } from "../../store/store";
import { useDispatch, useSelector } from "react-redux";

const logic = (textInput, dateInfo, calName) => {
 

  let out = textInput.replace(/\w+[A-Z]\w*/g, "");
  out = out.replace(/[S/M/T/W/F/]\w*/g, "\n").replace(/^\s*[\r\n]/gm, "");
  let re = new RegExp("-", "g");
  const arrey = out.split("\n");
  const newArrey = arrey.map((i) => {
    return i.replace(/[S/M/T/W/F/,]/g, "").replace(re, "");
  });

  const newObj = newArrey.map((i) => {
    let split = i.split(" ");
    let split2 = {};
 let keyToNumber = Number(split[1])

 //const stateOftime = split2[8]
 //console.log(split[1]);
 //console.log(split[2]< split[split.length -2]? 'day' : 'night');
 const isDay = split[2]< split[split.length -2]? split[1] : Number(split[1])+1

    if (split[2] === "Rest" ||split[2] === ''  ) {
      return {key: 100};
    
    } else  {
     // console.log(split[8] +'lopp1');
      split2 = {
        key:  keyToNumber,
        subject : calName,
        dateStart: split[1]+'/'+dateInfo +'/2023',
        dateEnd : isDay +'/'+dateInfo +'/2023',
        timeStart: split[2],
        timeEnd: split[split.length - 2],
        focus : false
      };
      
      //split[1]+'/'+dateInfo +'/2023'
      return split2;
    } 
  });

 // console.log(newObj);
 const newObj2 = newObj.sort((a,b) => (a.key- b.key ))
 //console.log(newObj2);
const  newObj3 = newObj2.filter(a => a.timeStart != undefined)
//console.log(newObj3);
;
  return newObj3
};

export default logic;


