import classes from "./arrangeCal.module.css";
import Alert from "react-bootstrap/Alert";
import { stageActions, calenderActions } from "../../store/store";
import { useDispatch, useSelector } from "react-redux";
import Buttons from "../buttons/buttons";
import { useState } from "react";
import logic from "../logic/logic2";

const InputCal = () => {
  const [inputTextState, setInputTextState] = useState("");

  const dispatch = useDispatch();
  const mounth = useSelector((state) => state.calender.month);
  const calName = useSelector((state) => state.calender.calName);



  const nextStepHandler = () => {
  dispatch(calenderActions.addFullCal(logic(inputTextState,mounth, calName)))
  dispatch(stageActions.nextStage()); 
  };

  const backStepHandler = () => {
    dispatch(stageActions.previousStage());
  };

  return (
    <div>
      <Alert variant="primary">
        <p>Copy the table from 'timesquare' exported PDF</p>
      </Alert>
      <textarea
      cols={500}
        className={classes.textInput}
        value={inputTextState}
        onChange={(e) => setInputTextState(e.target.value)}></textarea>
      <Buttons new={backStepHandler} next={nextStepHandler} />
    </div>
  );
};

export default InputCal;
