import { configureStore, createSlice } from "@reduxjs/toolkit";

const initState = { stage: 0 };

const stageSlise = createSlice({
  name: "stage",
  initialState: initState,
  reducers: {
    nextStage(state) {
      state.stage++;
    },
    previousStage(state) {
      state.stage--;
    },
    clearsStage(state) {
      state.stage = 0;
    },
  },
});

const initCalSlice = {
  month: "00",
  calName: "Work",
  calender: [
    {
      key: "00",
      dateStart: "00/00",
      dateEnd: "00/00",
      timeStart: "00:00",
      timeEnd: "00:00",
    },
  ],
};

const caldenderSlice = createSlice({
  name: "calender",
  initialState: initCalSlice,
  reducers: {
    addDateAndName(state, actions) {
      state.calName = actions.payload.nameCal;
      state.month = actions.payload.mounth;
    },
    addFullCal(state, actions) {
      state.month = state.month;
      state.calName = state.calName;
      state.calender = actions.payload;
    },
    
  },
});

const store = configureStore({
  reducer: {
    stage: stageSlise.reducer,
    calender: caldenderSlice.reducer,
  },
});

export default store;

export const stageActions = stageSlise.actions;
export const calenderActions = caldenderSlice.actions;
