import logo from "./logo.svg";
import "./App.css";

import Buttons from "./container/buttons/buttons";
import { useSelector } from "react-redux";
import Alert from "react-bootstrap/Alert";
import Button from "react-bootstrap/Button";

import InputCal from "./container/arrangeCal/arrangeCal";
import InitialDataInput from "./container/initinalDataInput/initinalDataInput";
import EditCal from "./container/editCal.js/editCal";
import { useEffect, useState } from "react";

function App() {
  const stage = useSelector((state) => state.stage.stage);
  const [show, setShow] = useState(true);

  const stageElement = () => {
    if (stage == 0) {
      return <InitialDataInput />;
    }
    if (stage == 1) {
      return <InputCal />;
    }
    if (stage == 2) {
      return <EditCal />;
    }
  };

  const log = () => {
    console.log(stage);
  };

  const AlartNormal = (
    <Alert className="alartNormal" show={show} variant="success">
      <Alert.Heading>IMPORTANT</Alert.Heading>
      <p>
        Please note that you use this tool at your own risk. While I strive for
        accuracy, I cannot guarantee the correctness and completeness of the
        calculations. You are solely responsible for any actions taken based on
        the app's output. Please review the exported CSV file to ensure the
        results are correct before using them.
        <br />
        <br />
        *this Version*
        <br />
        - Doesn not support mobile use.-
        <br />
        - Might need to manuely adjust night shift start/end dates at the last
        edit screen -
        <br />
        <a href="https://youtu.be/IFxOkZ3NJu4"> Link to tutorial</a>
      </p>
      <hr />
      <div className="d-flex justify-content-end">
        <Button onClick={() => setShow(false)} variant="outline-success">
          I Agree
        </Button>
      </div>
    </Alert>
  );

  const alartMobile = (
    <Alert variant="warning" className="alartMobile">
      <Alert.Heading>Hey, nice to see you</Alert.Heading>
      <p>
        I would like to inform you that this tool is optimized to work
        exclusively on desktop computers and is not currently supported on
        mobile devices. This decision was made due to certain
        limitations with TimeSquere.
      </p>
      <hr />
      <p className="mb-0">
      Thank you for your understanding
      </p>
    </Alert>
  );

  return (
    <div className="App">
      <header className="App-header">
        {AlartNormal}
        {alartMobile}

        {!show && stageElement()}
      </header>
      <p>@Ry Beta V1.01B.3</p>
    </div>
  );
}

export default App;
