import Buttons from "../buttons/buttons";
import Alert from "react-bootstrap/Alert";
import Table from "react-bootstrap/Table";
import { calenderActions, stageActions } from "../../store/store";
import classes from './editCal.module.css'

import { useDispatch, useSelector } from "react-redux";
import { useEffect, useMemo, useState } from "react";
import { ExportJsonCsv } from 'react-export-json-csv';

import React from "react";
import { legacy_createStore } from "@reduxjs/toolkit";

const EditCal = (props) => {
  const dataStore = useSelector((state) => state.calender.calender);
  const dataStoreFull = useSelector((state) => state.calender);

  const copyStore = [...dataStore];
  const [exportState, setExportState] = useState(copyStore);

  const ExportHeader = [
    {
      key: 'subject',
      name: 'Subject',
    },
    {
      key: 'dateStart',
      name: 'Start date',
    },
    {
      key: 'timeStart',
      name: 'Start time',
    },
    {
      key: 'dateEnd',
      name: 'End Date',
    },
    {
      key: 'timeEnd',
      name: 'End Time',
    },
  ]

  let disable = false

  const dispatch = useDispatch();

  const backStepHandler = () => {
    dispatch(stageActions.previousStage());
  };

  const finelExportStage = ()=> {
  //console.log(exportState);
  }

  const onValueChange = (value) => {
    value.preventDefault();
    const targetKey = value.target.id;
    const targetName = value.target.name;
    const targetValue = value.target.value;
    const findObj = exportState.find((e) => e.key == targetKey);
    const objIndx = exportState.findIndex((e) => e.key == targetKey);


    const newObj = {
      ...findObj,
      [targetName]: targetValue,
      auto: targetName,
    };

    const y = exportState.map((e, i) => {
      if (i === objIndx) {
        return newObj;
      } else {
        return e;
      }
    });
   // console.log(y);
    setExportState(y);
  };

  const list1 = exportState.map((e, i) => {
    return (
      <tr className="" key={e.dateStart + e.timeStart}>
        <td>
          <input
          size='12'
            autoFocus={e.auto == "dateStart" ? true : false}
            type="text"
            onChange={onValueChange}
            id={e.key}
            name="dateStart"
            value={e.dateStart}></input>
            
        </td>
        <td>
          {" "}
          <input
          size='12'
            autoFocus={e.auto == "timeStart" ? true : false}
            type="text"
            onChange={onValueChange}
            id={e.key}
            name="timeStart"
            value={e.timeStart}></input>
        </td>
        <td>
          {" "}
          <input
          size='12'
            autoFocus={e.auto == "dateEnd" ? true : false}
            type="text"
            onChange={onValueChange}
            id={e.key}
            name="dateEnd"
            value={e.dateEnd}></input>
        </td>
        <td>
          {" "}
          <input
          size='12'
            autoFocus={e.auto == "timeEnd" ? true : false}
            type="text"
            onChange={onValueChange}
            id={e.key}
            name="timeEnd"
            value={e.timeEnd}></input>
        </td>
      </tr>
    );
  });

  return (
    <div>
      <Alert variant="primary">
        <p>Edit Before Export</p>
      </Alert>
      <div >
        <Table className={classes.tables} striped bordered hover variant="dark" size="sm">
          <thead>
            <tr className={classes.tables}>
              <th>Starting Date</th>
              <th>Time Start</th>
              <th>Ending Date</th>
              <th>Time End</th>
            </tr>
          </thead>
          <tbody>{list1}</tbody>
        </Table>
       
      </div>

      <Buttons new={backStepHandler} next={finelExportStage} dis={true} />
      <ExportJsonCsv headers={ExportHeader} items={exportState}>Export</ExportJsonCsv>
      

    </div>
  );
};

export default EditCal;
